<template>
    <div>  
        <div class="loginNav">
            <!-- <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns"> -->
            <p class="title f30 cfff text-center">消息</p>
        </div>
        <div class="content">
            <van-cell-group>
                <van-cell title="请假审批" is-link to="/leave"/>
                <van-cell title="设备报修" is-link to="/repairList"/>
                <van-cell title="班级公告" is-link to="/classNotice"/>
                <van-cell title="家长私信" is-link to="/parentLetter"/>
                <van-cell title="家校互通" is-link />
            </van-cell-group>
        </div>
        

        <van-tabbar v-model="active" class="tabBar">
            <van-tabbar-item @click="turnToIndex"><span> 首页</span><img
                slot="icon"
                slot-scope="props"
                :src="props.active ? icon.hmeActive : icon.home"
            ></van-tabbar-item>
            <van-tabbar-item><span> 消息</span><img
                slot="icon"
                slot-scope="props"
                :src="props.active ? icon.msgActive : icon.msg"
            ></van-tabbar-item>
            <van-tabbar-item @click="turnToMy"><span> 我的</span><img
                slot="icon"
                slot-scope="props"
                :src="props.active ? icon.myActive : icon.my"
            > </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            active: 1,
            icon: {
                home: require('../../assets/my/home.png'),
                hmeActive: require('../../assets/my/homeActive.png'),
                msg:require('../../assets/my/msg.png'),
                msgActive:require('../../assets/my/msgActive.png'),
                my:require('../../assets/my/my.png'),
                myActive:require('../../assets/my/myActive.png')
            }
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        turnToMy(){
            this.$router.push({path:'/my'})
        },
        turnToIndex(){
            this.$router.push({path:'/indexs'})
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
    .van-tabbar-item--active {color: #8a9cff;}
    .tabBar{position: absolute;bottom: 0;left: 0;}
    
    .van-cell{
        height:1rem;
        line-height: 0.64rem;
    }
</style>




